.cnt-container svg .cnt-selected {
  color: #0066ff;
  fill: currentColor; }

.cnt-container svg .cnt-selectable {
  pointer-events: bounding-box; }

.cnt-container svg .cnt-selectable:hover {
  fill: #4d94ff; }

.cnt-container svg rect.cnt-areaSel {
  stroke: gray;
  stroke-dasharray: 4px;
  stroke-opacity: 0.5;
  fill: #0066ff;
  fill-opacity: .3; }

.cnt-container .cnt-emabox {
  display: none;
  background-color: #0066ff;
  padding: 5px;
  position: fixed;
  top: 1em;
  left: 1em; }
  .cnt-container .cnt-emabox .cnt-emaexpr {
    font-family: Helvetica, Arial, Sans-Serif;
    font-weight: bold;
    line-height: 2em;
    color: #fff;
    font-size: 30px; }
  .cnt-container .cnt-emabox .cnt-emabox-ctrls {
    float: right;
    margin-top: 0;
    margin-bottom: 5px; }
    .cnt-container .cnt-emabox .cnt-emabox-ctrls li {
      display: inline;
      list-style: none;
      margin-left: 5px;
      background-color: lightgrey;
      padding: 5px;
      border-radius: 3px; }
      .cnt-container .cnt-emabox .cnt-emabox-ctrls li a:link, .cnt-container .cnt-emabox .cnt-emabox-ctrls li a:hover, .cnt-container .cnt-emabox .cnt-emabox-ctrls li a:visited, .cnt-container .cnt-emabox .cnt-emabox-ctrls li a:active {
        text-decoration: none;
        color: black; }

.cnt-container .cnt-controls {
  position: fixed;
  top: 1em;
  right: 1em; }
  .cnt-container .cnt-controls .cnt-btn {
    position: relative;
    overflow: hidden;
    background-color: #0066ff;
    padding: 5px;
    margin-bottom: 10px;
    color: white;
    border-radius: 5px;
    cursor: pointer; }
    .cnt-container .cnt-controls .cnt-btn input[type=file] {
      position: absolute;
      top: 0;
      right: 0;
      min-width: 100%;
      min-height: 100%;
      font-size: 100px;
      text-align: right;
      filter: alpha(opacity=0);
      opacity: 0;
      outline: none;
      background: white;
      cursor: inherit;
      display: block; }
